.project {
  width: 300px;
  height: 300px;
  position: relative;
  cursor: pointer;
  // border: solid 2px red;
  .project-border {
    box-shadow: 15px 15px 0 rgb(50, 121, 64);
    border: 3px solid black;
    filter: url(#wavy);
    width: 297px;
    height: 297px;
    position: absolute;
    top: -1px;
    left: -1px;
    pointer-events: none;
    z-index: 50;
  }
  .project-image-wrapper{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .project-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
    filter: grayscale(1) ;
    

  }

  
  


  &:hover .project-image {
    transform: scale(1.05); // Slightly enlarge the image on hover
    
  }

  .project-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.717); // Gray overlay with opacity
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s ease;
    color: #fff;
    
  }
  

  &:hover .project-overlay {
    opacity: 1; // Show the overlay and content on hover
  }

  
  .project-title {
    font-size: 1.2rem;
    margin-bottom: 10px;
    text-align: center;
  }

  .project-tags {
    display: flex;
    justify-content: center;
    gap: 10px;
    flex-wrap: wrap;
  }

  .project-tag {
    padding: 5px 10px;
    border-radius: 5px;
    color: white;
    font-size: 0.9rem;
  }
}


